body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


* {
  z-index: 2;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}





@media (min-width: 1001px) {

    .carousel {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .mapemonde {
        width: 400px;
        height: 200px;
    }

    .carousel img {
        height: 200px;
        width: 200px;
        object-fit: cover;
    }

    .carousel-name {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: 200px;
        padding: 5px;
        margin: 10px;
        font-weight: 600;
        font-family: 'Texturina', serif;
        text-align: center;
        height: 25px;
    }

    .carousel-name button {
        border-radius: 40%;
        background-color: black;
        color: whitesmoke;
    }
    
    .school img {
        width: 200px;
        margin: 10px;
    }
    
    .profile {
        display: flex;
        align-items: center;
    }

    .profile-picture {
        height: 300px;
        width: 300px;
        object-fit: cover;
        border-radius: 50%;
        margin: 10px;
    }
    
    .profile ul li {
        list-style: none;
        text-align: left;
        font-size: 14px
    }
    .profile h1 {
        font-size: 40px;
        font-weight: 800;
    }
    .profile p {
        font-size: 14px;
    }
    .profile p, .profile h1, .profile ul li {
        font-family: 'Raleway', sans-serif;
        padding: 10px;
        margin: 5px
    }
    .carousels {
        display: flex;
        justify-content: space-evenly;
        align-items: flex-start;
        margin: 10px;
        padding: 10px;
    }
    .carousels h1 {
        font-family: 'Raleway', sans-serif;
        font-size: 20px;
        margin: 5px;
        text-align: center;
    }
    .passion-title {
        font-family: 'Raleway', sans-serif;
        font-size: 25px;
        text-align: center;
    }
}

