@media (min-width: 1001px) {

.nav {
    display: flex;
    justify-content: flex-end;
    
}
.logo {
    display: flex;
    margin: 10px 5px;
}
.logo img {
    width: 120px;
    height: 50px;
}


.nav-main {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #002395;
    background: linear-gradient(to bottom, #002395 0%, #001486 100%);
    background: -ms-linear-gradient(top, #002395 0%, #001486 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#002395', endColorstr='#001486', GradientType=0);
    box-shadow: inset 0 1px 0 #0F32A4;
    -webkit-box-shadow: inset 0 1px 0 #0F32A4;
    -moz-box-shadow: inset 0 1px 0 #0F32A4;
    height: 60px;

}
.nav-link a {
    text-decoration: none;
    color: black;
    margin: 10px 5px;
    font-size: 20px;
    background-color: rgba(255, 255, 255, 0.85);
    border-bottom-right-radius: 8px;
    border-top-left-radius: 8px;
    padding: 5px 20px;
    font-family: 'Bangers', cursive;
}
.nav-link a:hover {
    cursor: pointer;
    background-color: black;
    color: whitesmoke;
}
.hidden {
    visibility: hidden;
    height: 0px;
}

}


@media (max-width: 1000px) {
    section.visible {
        visibility: visible;
    }

    .hidden {
        visibility: hidden;
        height: 0px;
        width: 0px;
    }

    .nav {
        display: flex;
        justify-content: flex-end;
    }
    .logo {
        display: flex;
        margin: 10px 5px;
    }
    .logo img {
        width: 120px;
        height: 50px;
    }
    .nav-main {
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: #002395;
        background: linear-gradient(to bottom, #002395 0%, #001486 100%);
        background: -ms-linear-gradient(top, #002395 0%, #001486 100%);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#002395', endColorstr='#001486', GradientType=0);
        box-shadow: inset 0 1px 0 #0F32A4;
        -webkit-box-shadow: inset 0 1px 0 #0F32A4;
        -moz-box-shadow: inset 0 1px 0 #0F32A4;
        height: 60px;
        width: 100vw;
    }
    #phone-menu li {
        background: #002395;
        list-style: none;
        padding: 2px 5px;
        font-family: 'Bangers', cursive;
        position: relative;
        width: 97vw;
        z-index: 10;
        border-bottom: 1px solid #000f3f;
        
    }
    #phone-menu li a {
        text-decoration: none;
        color: white;
    }

    

}
